.art-modal {
  position: fixed;
  inset: 0;
  display: none;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  border: 1px solid black;
  z-index: 100;
  padding: 1rem;
  overflow-y: auto;

  &.is-active {
    display: flex;
    animation: fadeIn 0.3s ease-out forwards;
  }

  &__backdrop {
    position: absolute;
    inset: 0;
  }

  &__content {
    position: relative;
    background-color: #f6f6f6;
    max-width: 500px;
    width: 100%;
    max-height: 90vh;
    padding: 1rem;
    z-index: 1;
    display: grid;
    gap: 1.2rem;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.15);
    border: 1px solid black;
    animation: slideUp 0.3s ease-out forwards;
    overflow-y: auto;

    .art-modal__image {
      width: 100%;
      max-height: 320px;
      object-fit: contain;
      border-radius: 6px;
    }

    .art-modal__title {
      font-size: 1.8rem;
      font-weight: 500;
      border-top: 1px solid black;
      padding-top: 0.8rem;
      color: #000;
    }

    .art-modal__artist {
      font-size: 1rem;
      color: #555;
      font-weight: 400;
    }

    .art-modal__description {
      font-size: 1.1rem;
      color: #444;
      font-weight: 300;
      line-height: 1.5;
    }

    .art-modal__close {
      position: absolute;
      top: 1rem;
      right: 1.5rem;
      font-size: 2rem;
      background: none;
      border: none;
      color: #333;
      cursor: pointer;
      transition: transform 0.2s;

      &:hover {
        transform: scale(1.2);
      }
    }
  }
}
